import Box from '@mui/material/Box';
import styles from './articles.module.scss';
import classNames from 'classnames';
import { useEffect, useState } from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Grid from '@mui/material/Grid';
import { CircularProgress, Divider, Paper, Typography } from '@mui/material';

export interface ArticlesProps {
    className?: string;
}

interface RSSProfile {
    profileLink: string,
    profileImage: string,
    profileTitle: string
}

interface RSSItem {
    title: string,
    link: string,
    imageLink: string,
    categories: string[],
    description: string,
    publishedDate: string,
    updatedDate: string
}

/**
 * This component was created using Codux's Default new component template.
 * To create custom component templates, see https://help.codux.com/kb/en/article/configuration-for-articless-and-templates
 */
export const Articles = ({ className }: ArticlesProps) => {
    const [articles, setArticles] = useState<RSSItem[]>([]);
    const [profile, setProfile] = useState<RSSProfile | undefined>(undefined);
    const [loadingMessage, setLoadingMessage] = useState('Loading Articles...');
    const descriptionLength = 150;

    const rssFeed = 'https://thingproxy.freeboard.io/fetch/https://medium.com/feed/@oguo';
    const defaultImageLink = 'https://blog.depositphotos.com/wp-content/uploads/2017/07/Soothing-nature-backgrounds-2.jpg';

    const getRSSTextContent = (item: Element | Document, elName: string): string => {
        const el = item.getElementsByTagName(elName).item(0);
        if (el && el.textContent) {
            return el.textContent;
        } else {
            return '';
        }
    }

    const formatDate = (dateStr: string): string => {
        const d = new Date(dateStr);
        const today = new Date();

        const diffMS = today.getTime() - d.getTime();
        const daysAgo = Math.round(diffMS * (1 / 1000) * (1 / 60) * (1 / 60) * (1 / 24));
        const weeksAgo = Math.round(daysAgo / 7);
        const monthsAgo = Math.round(daysAgo / 30); 
        const yearsAgo = Math.round(monthsAgo / 12);

        if (yearsAgo != 0) {
            return `${yearsAgo}y`;
        } else if (monthsAgo != 0) {
            return `${monthsAgo}m`;
        } else if (weeksAgo != 0) {
            return `${weeksAgo}w`;
        } else {
            return `${daysAgo}d`;
        }
    }

    useEffect(() => {
        fetch(rssFeed).then(res => {
            if (res.ok) {
                res.text().then(rssXML => {
                    const parser = new DOMParser();
                    const xmlDoc = parser.parseFromString(rssXML, 'text/xml');

                    const items = Array.from(xmlDoc.getElementsByTagName('item'));
                    let rssItems: RSSItem[] = [];

                    items.forEach(item => {
                        const title = getRSSTextContent(item, 'title');
                        const link = getRSSTextContent(item, 'link');

                        const descriptionXML = getRSSTextContent(item, 'content:encoded');
                        const descriptionParsed = parser.parseFromString(descriptionXML, 'text/html');
                        
                        const imageLink = descriptionParsed.getElementsByTagName('img').item(0) ? 
                            descriptionParsed.getElementsByTagName('img').item(0)!.src : defaultImageLink;
                        
                        const description = descriptionParsed.getElementsByTagName('body').item(0) ? 
                            descriptionParsed.getElementsByTagName('body').item(0)!
                                .textContent!.substring(0, descriptionLength) + '...' : '';

                        const publishedDate = formatDate(getRSSTextContent(item, 'pubDate'));
                        const updatedDate = new Date(getRSSTextContent(item, 'atom:updated'))
                                                .toISOString().split('T')[0];

                        let categories: string[] = [];
                        const categoryItems = Array.from(item.getElementsByTagName('category'));

                        categoryItems.forEach(category => {
                            if (category && category.textContent) {
                                categories.push(category.textContent);
                            }
                        });
                        
                        let rssItem: RSSItem = {
                            title,
                            link,
                            imageLink,
                            description,
                            categories,
                            publishedDate,
                            updatedDate
                        };

                        rssItems.push(rssItem);
                    });

                    setArticles(rssItems);

                    const profile: RSSProfile = {
                        profileTitle: getRSSTextContent(xmlDoc, 'title'),
                        profileImage: getRSSTextContent(xmlDoc, 'url'),
                        profileLink: getRSSTextContent(xmlDoc, 'link')
                    }
                    
                    setProfile(profile);
                    
                });
            }
            else {
                setLoadingMessage(`Failed to load articles. Status code: ${res.status}, ${res.statusText}`);
                throw res.status;
            }
        }).catch(err => {
            setLoadingMessage(`An unexpected error has occured. ${err}`);
            throw err;
        });
    }, []);

    if (articles.length === 0) {
        return (
            <Box>
                <CircularProgress color = 'inherit' size = {40}/>
                <Typography variant = 'h6' display = {'inline'} paddingLeft = {2}>
                    {loadingMessage}
                </Typography>
            </Box>
        );
    } else {
        return (
            <Box className={classNames(styles.root, className)}>
                <a href = {profile!.profileLink}>
                    <img src = {profile!.profileImage} />
                    <Typography variant = 'h4'><b>
                        {profile!.profileTitle}</b>
                    </Typography>
                </a>
                <Divider />

                <Grid container spacing = {3} className = {styles.Grid}>
                    {
                        articles.map(article => {
                            return (
                                <Grid item md = {4} key = {article.title}>
                                    <Card className = {styles.Card}>
                                        <a href = {article.link}>
                                        <CardContent>
                                            <Typography variant = 'h5'>
                                                <b>{article.title}</b>
                                            </Typography>
                                                
                                            <Typography variant = 'h6'>
                                                {article.description} 
                                            </Typography>
                                            <p><i>Published {article.publishedDate} ago</i></p>
                                            <img src = {article.imageLink}/>
                                            <p>
                                                <b>Categories: </b>
                                                <i>
                                                    { article.categories.map(category => 
                                                    category.toLowerCase()).join(', ') }
                                                </i>
                                            </p>
                                        </CardContent>
                                        </a>
                                    </Card>
                                </Grid>
                            );
                        })
                    }
                </Grid>
            </Box>
        );
    }
};
