import styles from './contacts.module.scss';
import classNames from 'classnames';
import Box from '@mui/material/Box';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import IconButton from '@mui/material/IconButton';
import GitHubIcon from '@mui/icons-material/GitHub';
import EmailIcon from '@mui/icons-material/Email';
import PictureAsPdfIcon from '@mui/icons-material/PictureAsPdf';
import { Button } from '@mui/material';
import resumePDF from '../../assets/resume.pdf';

export interface ContactsProps {
    className?: string;
}

const linkedinURL = 'https://www.linkedin.com/in/obert-guo-160749240';
const githubURL = 'http://github.com/obertguo';
const email = 'mailto:guoober551@gmail.com';

/**
 * This component was created using Codux's Default new component template.
 * To create custom component templates, see https://help.codux.com/kb/en/article/configuration-for-contactss-and-templates
 */
export const Contacts = ({ className }: ContactsProps) => {
    return (
        // <div className={classNames(styles.root, className)}>Contacts</div>;
        <Box className={classNames(styles.root, className)}>
            <IconButton href = {linkedinURL}>
                <LinkedInIcon fontSize = 'large'/>
            </IconButton>

            <IconButton href = {githubURL}>
                <GitHubIcon fontSize = 'large'/>
            </IconButton>

            <IconButton href = {email}>
                <EmailIcon fontSize = 'large'/>
            </IconButton>

            <Button variant = 'contained' startIcon = {<PictureAsPdfIcon />}
                    href = {resumePDF} >
                Resume
            </Button>
        </Box>
    );
};
