import styles from './nav.module.scss';
import classNames from 'classnames';
import React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import CssBaseline from '@mui/material/CssBaseline';
import Divider from '@mui/material/Divider';
import Drawer from '@mui/material/Drawer';
import IconButton from '@mui/material/IconButton';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import MenuIcon from '@mui/icons-material/Menu';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import { Link } from 'react-router-dom';

export interface NavProps {
    className?: string;
}

/**
 * This component was created using Codux's Default new component template.
 * To create custom component templates, see https://help.codux.com/kb/en/article/configuration-for-navs-and-templates
 */

const navItems = ['Home', 'Articles'];
const drawerWidth = 300;

export const Nav = ({ className }: NavProps) => {
    const [drawerToggle, setDrawerToggle] = React.useState(false);

    const handleDrawerToggle = (): void => {
        setDrawerToggle(!drawerToggle);
    };

    const container = window !== undefined ? () => window.document.body : undefined;

    const drawer = (
        <Box onClick={handleDrawerToggle} sx = {{ textAlign: 'center' }}>
            <Typography variant = "h6">
                Obert Guo
            </Typography>

            <Divider />

            <List>
                {navItems.map((item) => {
                    return (
                        <Link to = {`/${item.toLowerCase()}`} 
                                className={styles.Link} key = {item}>
                            <ListItem disablePadding>
                                <ListItemButton sx = {{ textAlign: 'center' }}>
                                    <ListItemText primary = {item}/>
                                </ListItemButton>
                            </ListItem>
                        </Link>
                    );
                })}
            </List>
        </Box>
    );

    return (
        <Box className = {classNames(styles.root, className)} 
                sx = {{ display: 'flex' }}>
            <CssBaseline />
            <AppBar component = "nav"  className = {styles.nav}>
                <Toolbar>
                    <IconButton
                        color = "inherit"
                        aria-label = "open drawer"
                        edge = "start"
                        onClick = {handleDrawerToggle}
                        sx = {{display: { sm: 'none' } }}
                    >
                        <MenuIcon />
                    </IconButton>

                    {/* <Typography
                        variant="h6"
                        component="div"
                        sx={{ flexGrow: 1, display: { xs: 'none', sm: 'block' } }}>
                        <b>Obert Guo</b>
                    </Typography> */}

                    <Box sx = {{ display: { xs: 'none', sm: 'block' } }}>
                        {navItems.map((item) => {
                            return (
                                <Link to = {`/${item.toLowerCase()}`} 
                                    key = {item}> 
                                    <Button>
                                        {item}
                                    </Button>
                                </Link>
                            );
                        })}
                    </Box>
                </Toolbar>
                
            </AppBar>

            <Box component="nav">
                <Drawer
                    container={container}
                    variant="temporary"
                    open={drawerToggle}
                    onClose={handleDrawerToggle}
                    ModalProps={{
                        keepMounted: true, // better open performance on mobile
                    }}
                    sx={{
                        display: { xs: 'block', sm: 'none' },
                        '& .MuiDrawer-paper': {
                            boxSizing: 'border-box',
                            width: drawerWidth,
                        },
                    }}
                >
                    {drawer}
                </Drawer>
            </Box>
        </Box>
    );
};
